import * as tf from '@tensorflow/tfjs-core';
import * as tftsne from '@tensorflow/tfjs-tsne';
const ndarray= require('ndarray');
const nrrdjs = require('nrrd-js');
const uuidV1 = require('uuid/v1');
const scratch = require('ndarray-scratch');
const tsnejs = require('tsne');

export default class tfTsnePlugin {

    constructor() {
        this.jobGroup = new tfTsneJobGroup();
        this.monitor = new ReScatter.jobs.JobMonitor(tfTsnePlugin.getAlgorithmName());
    }

    get jobMonitor() {
        return this.monitor;
    }

    /**
     * Return a string name for the algorithm that can be used
     * to uniquely the algorithm in the configuration and
     * in the title of the generated data.
     * @returns {string}
     */
    static getAlgorithmName() {
        return 'TensorFlow-tSNE';
    }

    /**
     * Add data to calculate to the job group along with the
     * calculation parametersfor the tSNE algorithm
     * @param {ndarray} dataBuffer - the data to perform tSNE on
     * @param {object} calculation - tsne calculation parameters:
     *      calculation:{parameters: {tsne: {perplexity: }}}
     * @return {string} a unique key for the calculation
     */
    addJob(dataBuffer, calculation, id) {

        // If needed transpose the data so that tSNE occurs along the correct axis.
        // Default in this tsne implementation is COL so only need to transpose for ROW
        let calculationAxis = calculation.parameters.calcAxis; //axis for dimensionality reduction
        if (calculationAxis === ReScatter.data.PlotDataModel.axis.ROW) {
            dataBuffer = dataBuffer.transpose(1,0);
            dataBuffer = scratch.clone(dataBuffer);
        }
        let tsne_params = tfTsneJobGroup.default_tsne_params;
        // For Small numbers of points a large perplexity (default is 30)
        // makes no sense we use a value that is about 1/4 the number of points with
        // a lower limit of 2. In fact tsne for small numbers of points is not advisable
        if (dataBuffer.shape[0] < 100) {
            let perplexity = Math.ceil(dataBuffer.shape[0]/4) < 2 ? 2: Math.ceil(dataBuffer.shape[0]/4);
            console.log('Reducing perplexity to: ' + perplexity + ' for ' + dataBuffer.shape[0] + ' points in plot: ' + id);
            tsne_params.perplexity = perplexity;
        }
        if (calculation.parameters.algorithmParams) {
            if (calculation.parameters.algorithmParams.perplexity) {
                tsne_params.perplexity = calculation.parameters.algorithmParams.perplexity;
            }
            if (calculation.parameters.algorithmParams.exact) {
                tsne_params.exact = calculation.parameters.algorithmParams.exact;
            }
        }
        this.jobGroup.addJob(dataBuffer, id, tsne_params);
        let dataKey = this.getJobDataKey(id);
        return dataKey;
    }

    /**
     * Make a unique data key using the job pubsubid and append a suffix
     * that is specific to a data item.
     * @param {string} dataSuffix
     * @returns {string}
     */
    getJobDataKey(dataSuffix) {
        return this.jobGroup.pubsubid + '_analysis_' + dataSuffix;
    }

    /**
     * Start the plugin job.
     * @param resultCallback
     * @param failCallback
     */
    start(resultCallback, failCallback) {
        // The job group returns a result pair - jobId and
        // and ndarray of coordinates.
        // The coordinates are compressed to nrrd for compatibility with the
        // job protocol.
        let unpackCallback = (results) =>{
            let coordinates = [];
            for (let result of results) {
                // Compress coords as an nrrd object.
                let compressedResult = nrrdjs.serialize({data: result[1].data, sizes: result[1].shape.slice().reverse()});
                coordinates.push([result[0], compressedResult]);
            }
            resultCallback(coordinates);
        };
        let jobCallback = (msg, data) => {
            //console.log('job: ' + msg + ' status: ' + data.status);
            if (data.status === "success") {
                let client = this.jobMonitor.getResultClient(msg);
                client.then(unpackCallback, failCallback);
            }
        };
        PubSub.subscribe(this.jobGroup.pubsubid, function(msg,data) {
            jobCallback(msg, data);
        });
        if (this.jobGroup.jobs.length > 0) {
            this.jobMonitor.addJobGroup(this.jobGroup); // autostarts the job
        }
    }
}

// This resembles the DimRes client JobGroup but the
// calculation while asynchronous is performed in the browser
// using the GPU via tensorflowjs tSNE
class tfTsneJobGroup {

    static get default_tsne_params() { return {
        'n_jobs': 1,
        'n_iter': 1000,
        'n_components': 2,
        'perplexity': 30
    };}

    constructor(restURL) {
        this.optimizers = [];
        this.optimizers.push = (val) =>{
            this.optimizers[this.optimizers.length] = val;
            if (this.optimizers.length == 1) {
                this.status = DimRS.JobGroup.JobStatus.QUEUED;
            }
        };
        this.pubsubid = 'JOBGROUP_' + uuidV1();
        this.stat = DimRS.JobGroup.JobStatus.CREATED;
        this.failmsg = undefined;
        this.resultPairs = [];
        this.jobsdone = 0;
        this.jobs = [];
        this.createdtime = Date();
        this.cancelFlag = false;
        this.maximumTextureSize = this.getMmaxTextureSize();
    }



    get pubsubTopic() {
        return this.pubsubid;
    }

    get status() {
        return this.stat;
    }

    get maxTextureSize() {
        return this.maximumTextureSize;
    }

    /**
     * Set the status - if the status changes from the previous value a PubSub event
     * is emitted
     * @param val - new status value
     */
    set status(val) {
        if (this.stat != val) {
            this.stat = val;
            PubSub.publish(this.pubsubTopic, {status: this.stat, jobsDone: this.jobsdone});
        }
    }

    async reportJobProgress(jobId, data, progressString) {
        PubSub.publish(this.pubsubTopic,
            {status: DimRS.JobGroup.JobStatus.PROGRESS,
                jobId: jobId,
                progressData: {data: data, string: progressString}});
        //await this.sleepPromise(10);
    }

    get jobIds () {
        const jobIds = [];
        for (let job of this.jobs) {
            jobIds.push(job.callerJobId);
        }
        return jobIds;
    }

    get numSubJobs () {
        return this.jobs.length;
    }

    get numComplete () {
        return this.jobsdone;
    }

    /**
     * Change the number of completed jobs - will trigger a PubSub event
     * if the value is different from before. As a side effect the status may go to
     * RUNNING or SUCCESS ir appropriate.
     * @param val
     */
    set numComplete (val) {
        if (this.jobsdone != val) {
            this.jobsdone = val;
            if (this.jobsdone > 0 &&  this.jobsdone < this.numSubJobs && this.status != DimRS.JobGroup.JobStatus.RUNNING) {
                this.status = DimRS.JobGroup.JobStatus.RUNNING;
            }
            else if (this.jobsdone == this.numSubJobs) {
                this.status = DimRS.JobGroup.JobStatus.SUCCESS;
            } else {
                PubSub.publish(this.pubsubTopic, {status: this.stat, jobsDone: this.jobsdone});
            }
        }
    }

    get failMessage() {
        return this.failmsg;
    }

    get results() {
        return this.resultURLs;
    }

    getMmaxTextureSize() {
        const backend = tf.ENV.findBackend('webgl');
        if (backend === null) {
            return 0
        }
        const gpgpu = backend.getGPGPUContext();
        return gpgpu.gl.getParameter(gpgpu.gl.MAX_TEXTURE_SIZE);
    }
    /**
     * Add another tsne job to the group. Jobs are grouped
     * for ease of monitoring jobs related to a single layout.
     * @param {ndarray} dataBuffer
     * @param {string} callerJobId
     * @param {object} tsne_params
     */
    addJob(dataBuffer, callerJobId, tsne_params) {

        this.jobs.push({'buffer': dataBuffer,
                        'callerJobId': callerJobId,
                        'params': tsne_params || DimRS.JobGroup.default_tsne_params}
        );
    }

    /**
     * start the job. Return an array (poor-man's tuple) containing
     * job id and tsne coordinates
     */
    start() {
        const tDataStore = [];
        for (let job of this.jobs) {
            let tOpt = this.__createTsneOpt(job, tDataStore);
            console.log(`Added job, shape ${job.buffer.shape}, perplexity: ${job.params.perplexity}`);
            this.optimizers.push([job, tOpt]);
        }
        // submit all of the configured jobs
        const self =this;
        const execTsneFunc = async function(pair, idx) {
            console.log('Id: '+ idx + ' start: ' + Date.now());
            const coords = await self.iterativeTsne(pair[0], pair[1]);
            console.log('Id: '+ idx + ' end: ' + Date.now());
            // Unpack the coordinates to an ndarray
            const ndResult = ndarray(coords, [coords.length/2, 2]);

            pair = [pair[0].callerJobId, ndResult];
            return pair;
        };
        // Run tsnes sequentially instead of Promise.all
        // this appears to give the best balance of performance/responsiveness
        // with current Chrome (69) and Firefox (62) - Sept 2018
        // This may of course change.
        (async () => {let counter = 0;
            for (let pair of this.optimizers) {
                try {
                    const result = await execTsneFunc(pair, counter);
                    this.resultPairs.push(result);
                    counter++;
                    this.numComplete++;
                } catch(reason) {
                    this.failmsg = reason;
                    this.status = DimRS.JobGroup.JobStatus.FAIL;
                    // force the execution to stop by signalling all jobs complete
                    this.numComplete = this.numSubJobs;
                    break;
                }
            }
            for (let tData of tDataStore) {
                tData.dispose();
            }
        })();
    }

    /**
     * Create either a pure tsne optimizer or a tensorflow texture
     * tsne optimizer. The exact field in the job parameters determines
     * which.
     * @param job - the job - including parameters and datd
     * @param tensorStore [] - save any tensors here for later cleanup
     * @returns {*} - a tsneOpt object
     * @private
     */
    __createTsneOpt(job, tensorStore) {
        // the job.buffer is an ndarray
        let tsneOpt = null;
        if (job.params.exact) {
            let opt = {
                epsilon: 10,
                perplexity: job.params.perplexity,
                dim: 2
            };
            tsneOpt = new tsnejs.tsnejs.tSNE(opt);
            let points = [];
            for(let p = 0; p < job.buffer.shape[0]; p++) {
                let row = [];
                for(let d = 0; d < job.buffer.shape[1]; d++) {
                    row.push(job.buffer.get(p,d));
                }
                points.push(row);
            }
            tsneOpt.initDataRaw(points);

        } else {
            let data = job.buffer;
            const tData = tf.tensor(Float32Array.from(data.data), data.shape);
            tensorStore.push(tData);
            tsneOpt = tftsne.tsne(tData, {
                perplexity: job.params.perplexity,
                verbose: false
            });
        }
        return tsneOpt;
    }

    sleepPromise(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async __getCoordData(tsneOpt, job) {
        if (job.params.exact) {
            let coordArray = tsneOpt.getSolution();
            let coordSeq = [];
            for (let coord of coordArray) {
                coordSeq.push(...coord);
            }
            return Float32Array.from(coordSeq);
        } else {
            return await tsneOpt.coordinates().data();
        }

    }
    /**
     * Perform the tensorflow tsne iteratively to prevent
     * freezing the browser.
     * TODO - work is underway to allow tensorflow to work with OffscreenCanvas
     * allowing this to be performed in a Web Worker thread (https://github.com/stitchfix).
     * Currently (July 2018) OffscreenCanvas is available in Chrome
     * and Firefox and has to be explicitly enabled.
     * @param tsneOpt
     * @returns {Promise<*>}
     */
    async iterativeTsne(job, tsneOpt) {
        // Get the suggested number of iterations to perform.
        const maxKnnIterations = 160;
        const minKnnIterations = 60;
        let knnIterations = 0;
        if (!job.params.exact) {
            knnIterations = Math.max(
                Math.min(tsneOpt.knnIterations(), maxKnnIterations),
                minKnnIterations);
        }
        const ksteps = 2;
        const ksleepMs = 80;
        const tsteps = 5;
        const tsleepMs = 10;
        // cheat move to config TODO
        const tsneIterations = 1000;
        // Do the KNN computation. This needs to complete before we run tsne
        //await tsneOpt.compute();
        for(let i = 0; i < knnIterations; i+=ksteps){
            if (this.cancelFlag) {
                return tsneOpt;
            }
            try {
                await tsneOpt.iterateKnn(Math.min(ksteps, knnIterations-i));
            } catch(e) {
                this.status = DimRS.JobGroup.JobStatus.FAIL;
                console.log('Job failed in knn due to: ' + e);
                return tsneOpt;
            }
            // You can update knn progress in your ui here.
            await this.reportJobProgress(job.callerJobId, [], `knnIteration ${i}/${knnIterations}`);
            await this.sleepPromise(ksleepMs)
        }
        await this.reportJobProgress(job.callerJobId, [], `knnIteration ${knnIterations}/${knnIterations}`);
        let coordData;
        for(let i = 0; i < tsneIterations; i+=tsteps) {
            if (this.cancelFlag) {
                return tsneOpt;
            }
            try {
                if (job.params.exact) {
                    tsneOpt.step();
                } else {
                    await tsneOpt.iterate(Math.min(tsteps, tsneIterations - i));
                }
            } catch(e) {
                this.status = DimRS.JobGroup.JobStatus.FAIL;
                console.log('Job failed in tsne grad descent due to: ' + e);
                return tsneOpt;
            }
            // Draw the embedding here...

            coordData = await this.__getCoordData(tsneOpt, job);
            await this.reportJobProgress(job.callerJobId, coordData, `tsneIteration ${i}/${tsneIterations}`);
            await this.sleepPromise(tsleepMs);

        }
        await this.reportJobProgress(job.callerJobId, coordData, `tsneIteration ${tsneIterations}/${tsneIterations}`);
        return await this.__getCoordData(tsneOpt, job);
    }

    cancel(force) {
        let forced = force || false;
        // Deleting the jobs in the server will
        this.cancelFlag = true;
        this.status = DimRS.JobGroup.JobStatus.CANCEL;
    }

    /**
     * Return a promise that resolves to a list of coordinate results
     * In this case it could be synchronous but this interface allows
     * for the asynchronous case when the results must be fetched from the server
     */
    get_result() {
        return Promise.resolve(this.resultPairs).then(values => {
            return values;
        });
    }
}
