/**
 * A set of plugins to renormalize the Brainscope data for sub selections.
 * Each ReScatter based website will have different
 */

const nrrdjs = require('nrrd-js');
const ndarray = require('ndarray');
const pool = require("ndarray-scratch");
const unsqueeze = require('ndarray-unsqueeze');
const concat_col = require('ndarray-concat-cols');
const concat_row = require('ndarray-concat-rows');
const show = require('ndarray-show');
const ops = require('ndarray-ops');
const samp_norm = require('ndarray-sample-normalize');
const frame = require('ndarray-frame');
var FileSaver = require('file-saver');

let normalize_sample = require('ndarray-sample-normalize');

/**
 * Renormalization for the Brainscope dual explorer layout
 */
class PreprocessDualExplorer {
    static getPreprocessingName() {
        return 'Brainscope Dual View Preprocess';
    }
    constructor() {this.dataMap = {};}


    __zscoreInplaceRow(data) {
        for (let i =0, len = data.shape[0]; i < len; i++ ) {
            samp_norm(data.pick(i, null));
        }
    }

    __zscoreInplaceCol(data) {
        for (let i =0, len = data.shape[1]; i < len; i++ ) {
            samp_norm(data.pick(null, i));
        }
    }

    __zscoreInplace3DRow(data) {
        for (let i =0, len = data.shape[2]; i < len; i++ ) {
            this.__zscoreInplaceRow(data.pick(null, null,i));
        }
    }

    __zscoreInplace3DCol(data) {
        for (let i =0, len = data.shape[2]; i < len; i++ ) {
            this.__zscoreInplaceCol(data.pick(null, null, i));
        }
    }
    // Subselect rows of columns, then add all subject data and divide by number of subjects
    // data: 3d: dim 1 row - dim 2 col, dim 3 subject
    // selection: row or column indices
    // axis: row or column axis (according to ReScatter.data.PlotDataModel.axis enum)
    // return: two ndarray containing the average of the subsample subject data
    __subsetSubjectData(data, selection, axis) {
        let assemList = [];
        let r = null;
        // subsample row or column - note unsqueeze to keep the singleton dimension
        if (axis === ReScatter.data.PlotDataModel.axis.ROW) {
            selection.forEach(i => assemList.push(unsqueeze(data.pick(i, null, null), 0)));
            r = concat_row(assemList, {dtype: 'float32'});
        } else {
            let colSwap = data.transpose(1,0,2);
            // unsqueeze feature? cannot insert dimension in the middle so work transposed
            selection.forEach(i => assemList.push(unsqueeze(colSwap.pick(i, null, null), 0)));
            let q = concat_row(assemList, {dtype: 'float32'});
            r = q.transpose(1,0,2);
        }

        return r;
    }

    __averageSubjects(data) {
        // sum over the subjects
        let numSubjects = data.shape[2];
        let r = pool.zeros(data.shape.slice(0,2));
        for (let i = 0; i < numSubjects; i++) {
            ops.addeq(r, data.pick(null, null, i));
        }
        // calculate mean
        ops.divseq(r, numSubjects);
        return r;
    }

    __saveExpressionToCache(cache, data, dataKey) {
        // not the default stride so copy the incoming data
        let tdata = pool.clone(data.transpose(1,0));
        let cacheBuffer = nrrdjs.serialize({data: tdata.data, sizes: tdata.shape.slice().reverse()});
        cache.insertInCache(cacheBuffer, dataKey, true);
    }

    __subsetSampleIds(ids, selection, axis) {
        if (axis === ReScatter.data.PlotDataModel.axis.COL) {
            return ids.filter((e,i) => selection.includes(i));
        }
        return ids;
    }

    __saveToFile(typedArray, name) {
        let blob = new Blob([typedArray.buffer], {type: 'application/octet-stream'});
        FileSaver.saveAs(blob, name);

    };
}

ReScatter.protocols.DataPreprocessProtocol.impl(PreprocessDualExplorer, [], {

    getRecalculationData(operationGuid, dataCache, recalcConfig, selectionIndexes, selectionId) {
        // Retrieve the original data
        // Expect a single base data file in the dual explorer
        // data is 19992x105x6
        let nrrdfile = nrrdjs.parse(dataCache.getFromCache(recalcConfig.baseDataMaps[0].filePath));
        let originalData = ndarray(nrrdfile.data, nrrdfile.sizes.slice().reverse());
        let rcsData = originalData.transpose(2,1,0); //Correct - yes the nrrd returns subject, col, row need to reverse
        let axis = recalcConfig.dataAnalysis.dataPreprocess.basemapSelections[selectionId];
        // TODO retrieve via config
        let choroMap = dataCache.getFromCache("/static/data/smhh_01/AH_map_choropleth_to_samples.json");
        let sampProps = dataCache.getFromCache("/static/data/human105SampleProperties.json");
        let regionMap = choroMap.ordered_region_map;

        // Initial subset and z-score
        let all_genescale = this.__subsetSubjectData(rcsData, selectionIndexes, axis);
        let all_samplescale = pool.clone(all_genescale);

        this.__zscoreInplace3DRow(all_genescale);
        this.__zscoreInplace3DCol(all_samplescale);

        // subject average and z-score
        let mean_genescale = this.__averageSubjects(all_genescale);
        this.__zscoreInplaceRow(mean_genescale);
        let mean_samplescale = this.__averageSubjects(all_samplescale);
        this.__zscoreInplaceCol(mean_samplescale);
        //this.__saveToFile(Float32Array.from(mean_genescale.data), 'genescale.bin');
        //this.__saveToFile(Float32Array.from(mean_samplescale.data), 'samplescale.bin');

        // make the sample column data frame
        let sampleIds = this.__subsetSampleIds(sampProps.voxel_props.id, selectionIndexes, axis);
        // choro ids are integer but the maps ar string - force col labels to string
        let choroFrame = new frame(mean_genescale, sampleIds.map(x => Number(x).toString()));
        // for each choroRegion get the mean expression
        let choroExpColumns = [];
        for (let mapEl of regionMap) {
            choroExpColumns.push(choroFrame.mean(...mapEl[1]));
        }

        let display_choroscale = concat_col(choroExpColumns);


        // The zscore in the other direction for the display values
        let display_genescale = pool.clone(mean_samplescale);
        this.__zscoreInplaceRow(display_genescale);
        let display_samplescale = pool.clone(mean_genescale);
        this.__zscoreInplaceCol(display_samplescale);
        // The mean_genescale is the basis for the choropleth display
        // with some mapping and zscore
        // let display_choroscale = pool.clone(mean_genescale);

        // The filling of the data cache is hard coded.
        // Refactor for better config driven approach.
        this.dataMap = {
            meanDonor: 'meanDonor' + '_' + operationGuid,
            meanDonor_sample: 'meanDonor_sample' + '_' + operationGuid,
            meanDonorSvg_sample: 'meanDonorSvg_sample' + '_' + operationGuid
        };
        // save the datamap files in the cache with keys
        this.__saveExpressionToCache(dataCache, display_genescale, this.dataMap['meanDonor']);
        this.__saveExpressionToCache(dataCache, display_samplescale, this.dataMap['meanDonor_sample']);
        this.__saveExpressionToCache(dataCache, display_choroscale, this.dataMap['meanDonorSvg_sample']);

        //this.__saveExpressionToCache(display_choroscale, 'meanDonorSvg_sample' + '_' + operationGuid);
        //return {'meanDonor': mean_genescale, 'meanDonor_sample': mean_samplescale};
        // seem to be flipped
        return {
            'meanDonor': pool.clone(mean_genescale.transpose(1,0)),
            'meanDonor_sample': pool.clone(mean_samplescale.transpose(1,0))};
    },

    getDataMap() {
        return this.dataMap;
    }

});
